import { Component, EventEmitter, Input, OnInit, Optional, Output } from '@angular/core';
import { CardsBackendError, CardsService, filterInputParameters, PaymentCard } from '@backbase/cards-common-ang';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { WidgetPropertiesService } from '../../services/widget-properties.service';

@Component({
  selector: 'bb-cards-list-widget',
  templateUrl: './cards-list-widget.component.html',
  providers: [CardsService, WidgetPropertiesService],
})
export class CardsListWidgetComponent implements OnInit {
  private readonly notificationTtlSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly updateAllLimitsSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly groupByPaymentCardTypesSubject = new BehaviorSubject<string[] | undefined>(undefined);

  /**
   * Notification timeout timer value configuration
   */
  // eslint-disable-next-line
  @Input('notificationTtl')
  set notificationTtlValue(value: number | undefined) {
    this.notificationTtlSubject.next(value);
  }
  /**
   * Enable to send multiple limits to the API when updating values
   */
  @Input('updateAllLimits') set updateAllLimitsValue(value: boolean | undefined) {
    this.updateAllLimitsSubject.next(value);
  }
  /**
   * Types of cards configuration value
   */
  // eslint-disable-next-line
  @Input('groupByPaymentCardTypes')
  set groupByPaymentCardTypesValue(value: string | undefined) {
    const newArray = value?.split(',').filter((val) => val.length > 0);

    this.groupByPaymentCardTypesSubject.next(newArray);
  }
  /**
   * Event emitter for selecting a card.
   */
  @Output() readonly selectedCard = new EventEmitter<string>();
  /**
   * Observable of payment cards.
   */
  paymentCards: Observable<Array<PaymentCard>> | undefined;
  /**
   * Error message in case something unexpected occurs.
   */
  errors: CardsBackendError[] | undefined;

  constructor(
    private readonly cardsService: CardsService,
    @Optional() private readonly widgetPropertiesService: WidgetPropertiesService,
  ) {}

  private readonly handleError = (errors: CardsBackendError[]) => {
    this.errors = errors;

    return of([]);
  };

  ngOnInit() {
    this.cardsService.setParameters(this.groupByPaymentCardTypes);
    this.paymentCards = this.cardsService.getCards().pipe(catchError(this.handleError));
  }

  get groupByPaymentCardTypes(): Observable<string[]> {
    return filterInputParameters(
      this.groupByPaymentCardTypesSubject,
      this.widgetPropertiesService.groupByPaymentCardTypes,
    );
  }

  get notificationTtl(): Observable<number> {
    return filterInputParameters(this.notificationTtlSubject, this.widgetPropertiesService.notificationTtl);
  }

  get updateAllLimits(): Observable<boolean> {
    return filterInputParameters(this.updateAllLimitsSubject, this.widgetPropertiesService.updateAllLimits);
  }

  onSelectCard(paymentCard: PaymentCard) {
    this.selectedCard.emit(paymentCard.id);
  }
}
