import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PaymentCard } from '@backbase/cards-common-ang';

@Component({
  selector: 'bb-payment-card-group',
  templateUrl: './payment-card-group.component.html',
})
export class PaymentCardGroupComponent implements OnInit {
  /**
   * Title to be displayed over payment cards.
   */
  @Input() heading: string | undefined;
  /**
   * Array of payment cards to be displayed.
   */
  @Input() paymentCards: Array<PaymentCard> | undefined;
  /**
   * Specifies the time that a notification would be displayed
   */
  @Input() notificationTtl: number | undefined;
  /**
   * Enable to send multiple limits to the API when updating values
   */
  @Input() updateAllLimits: boolean | undefined;
  /**
   * Event emitter for selecting a card.
   */
  @Output() readonly selectCard = new EventEmitter<PaymentCard>();

  ngOnInit() {
    if (typeof this.paymentCards === 'undefined') {
      throw new Error(`"paymentCards" input is required in "${this.constructor.name}"`);
    }
  }

  onSelectCard(paymentCard: PaymentCard) {
    this.selectCard.emit(paymentCard);
  }
}
